:root{
  --bg-black-900:#f2f2fc;
  --bg-black-100:#fdf9ff;
  --bg-black-50:#e8dfec;
  --text-black-900:#302e4d;
  --text-black-700:#504e70;
}
body{

  line-height: 1.5;
  font-size: 16px;
}
*{
  margin: 0;
  padding: 0;
  outline: 0;
  text-decoration: 0;
  box-sizing: border-box;
}
::before,::after{
  box-sizing: border-box;
}
