@import url('https://fonts.googleapis.com/css2?family=Clicker+Script&family=Poppins:wght@200;300;400;500;600&family=Urbanist:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root{
    --bg-black-900:rgba(225,225,240,1);
    --bg-black-500:#fdf9ff;
    --bg-black-100:#fdf9ff8f;
    --bg-black-50:#e8dfec;
    --text-black-900:#302e4d;
    --text-black-700:#504e70;
    --skin-color:#ec1839;
    --color1:#ec1839;
    --color2:#fa5b0f;
    --color3:#37b182;
    --color4:#1854b4;
    --color5:#f021b2;
  }
  body{
  
    line-height: 1.5;
    font-size: 16px;
    font-family: "Poppins", sans-serif;
  }
  *{
    margin: 0;
    padding: 0;
    outline: 0;
    text-decoration: 0;
    box-sizing: border-box;
  }
  ::before,::after{
    box-sizing: border-box;
  }
  .padd-15{
    padding-left: 15px;
    padding-right: 15px;
}
.main-container{
  z-index: 15;
}
.main-container.color-1{
  --skin-color:var(--color1);
}
.main-container.color-2{
  --skin-color:var(--color2);
}
.main-container.color-3{
  --skin-color:var(--color3);
}
.main-container.color-4{
  --skin-color:var(--color4);
}
.main-container.color-5{
  --skin-color:var(--color5);
}
.main-container.dark{
  --bg-black-900:#151515;
  --bg-black-500:#222222;
  --bg-black-100:#222222d3;
  --bg-black-50:#393939;
  --text-black-900:#ffffff;
  --text-black-700:#e9e9e9;
}
.background.dark{
  --bg-black-900:#151515;
}